.nav a{
   text-decoration: none;
   padding: 1rem 2rem;
    background-color: rgb(29, 28, 28);
    color: rgb(184, 163, 163);
    border-radius: 4px;
    box-shadow: 0 1rem 1.2rem rgb(29, 28, 28);
    border: 1px solid #111;
    font-size: 4vh;
}
.nav a:link ,.nav a:visited{
   background-image: linear-gradient(120deg, transparent 0%, transparent 50%, #545a5b 50%);
   background-size: 290%;
  transition: all 2s;
}
.nav a:hover{
   background-position: 100%;
   color: #42a915;
   
}
.nav a.active,
.nav a:active
{
   background: #fff;
   border-bottom: 4px solid #40A4C8;
}