.basket{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.3rem; 
    padding: 1vh 0;
}
.svg{
    padding: .5rem 1rem;
    background: rgb(80, 217, 225);
    border: 1px solid rgb(80, 217, 225);
    font-size: 2rem;
    letter-spacing: 3px;
    transition: all 1s ease;
    
}
.svg:hover{
    background: rgb(10, 142, 149);
    box-shadow: 0 .2rem .3rem rgb(4, 79, 83);
}
.svg:active{
    background: rgb(5, 108, 113);
}
.less{
    padding: .5rem;
    background: rgb(80, 217, 225);
    border: 1px solid rgb(80, 217, 225);
    font-size: 2rem;
    letter-spacing: 3px;
   margin-right: .4rem;
   transition: all 1s ease;
    
}
.less:hover{
    background: rgb(10, 142, 149);
    box-shadow: 0 .2rem .3rem rgb(4, 79, 83);
}
.less:active{
    background: rgb(5, 108, 113);

}
.some_box{
    position: relative;
}
.some{
    width: 1.6rem;
    height: 1.6rem;
    background: rgb(111, 113, 5);
    border-radius: 50%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 2.7rem;
    right: -.4rem;
}

@media(max-width:500px){
    .basket{
        margin-bottom: 1rem;
    }
    .svg{
        font-size: 1rem;
        padding: .3rem .8rem;
    }
    .less{
        font-size: 1rem;
        padding: .3rem .8rem;
    }
    .some{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        bottom: 1.7rem;
    right: -.3rem;
    }
}
