.openList{
    width: 8vh;
    height: 8vh;
    border-radius: 50%;
    z-index: 3000;
    position: fixed;
    left: 5vh;
    top: 1vh;
    transition: 2s;
}
.openListrotate{
    z-index: 3000;
    transform: rotate(180deg);
}
.List{
    position: relative;
    padding: 10vh 0 6vh 0;
    width: 100%;
    min-height: 20vh;
    
    max-height: 100vh;
    overflow-y:auto ;
    overflow-x: hidden;
    background: rgb(248, 237, 237);
    position: fixed;
    top: 0;
    
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    flex-flow: row wrap;
    animation: open 2.5s forwards;
    z-index:  800;
}
.ListClose{
    position: relative;
    padding: 10vh 0 6vh 0;
    width: 100%;
    min-height: 20vh;
    
    max-height: 100vh;
    overflow-y:auto ;
    overflow-x: hidden;
    background: rgb(248, 237, 237);
    position: fixed;
    top: 0;
    
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    flex-flow: row wrap;
    animation:close 2.5s forwards;
}
.goback{
    padding: 1vh 2vh;
    font-size: 2.5vh;
    border: none;
    outline: none;
    background: rgb(27, 26, 26);
    color: #fff;
    border-radius: 5px;
    transition:all 1s ease;
    position: fixed;
    top: 2.5vh;
    right: 4vh;
}
.go{
    padding: 1vh 2vh;
    font-size: 2.5vh;
    border: none;
    outline: none;
    background: rgb(27, 26, 26);
    color: #fff;
    border-radius: 5px;
    transition:all 1s ease;
    position: fixed;
    right: 8vh;
    top: 10vh;
    
}
.goback:hover{
    background: rgb(59, 58, 58);
}
.item{
    width: calc(100%/6);
    margin-bottom: 7vh;
   display: flex;
   align-items: center;
   justify-content: center;
    font-size: 2.4vh;
    font-family: Georgia, 'Times New Roman', Times, serif;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: rgb(26, 25, 25);
    transition: all 1s ease;
    cursor: pointer;
    
}
svg{
    margin-left: 1rem;
}
.item:hover{
    color: rgb(22, 53, 189);
}

@keyframes open{
    from{
        position: fixed;
        top: -120vh;
    }
    to{
        position: fixed;
        top: 0vh;
    }
}
@keyframes close{
    from{
        position: fixed;
        top: 0vh;
    }
    to{
        position: fixed;
        top: -120vh;
        display: none;
    }
}

@media (max-width:850px){
    .item{
        font-size: 2vh;
    }
}
@media (max-width:750px){
    .item{
        font-size: 2vh;
        width: calc(100%/4);
    }
}

@media (max-width:530px){
    .item{
        font-size: 1.5vh;
        width: calc(100%/4);
    }
    svg{
        margin-left: .5rem;
    }
}
@media (max-width:450px){
    .item{
        font-size: 1.5vh;
        width: calc(100%/3);
    }
}