.scr{
    background: rgb(41, 208, 188);
    width: 100%;
}
.section{
    display: flex;
    
    justify-content: space-around;
    width: 90%;
   margin: 0 auto;
    
    flex-flow: row wrap;
}
.card{
    width:28em;
    height: 28rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    border: 1px solid rgb(37, 118, 189);
    box-shadow: 0 1rem 2rem rgb(13, 81, 141);
    border-radius: 4px;
    margin: 2rem 0;
    
    transition: all 3s ease-in;
}
.divimg{
    width: 65%;
    height: 15rem;
    display: flex;
    justify-content: center;
    align-content: center;
    overflow: hidden;
}
.card_img{
    object-fit: cover;
    transition: all 1s ease;
}

.card_img:hover{
    transform: scale(1.085);
    overflow: hidden;
}

.card_name{
    font-family: sans-serif;
    margin-bottom: 1.3rem;
}
.card_text{
     margin:0 1rem 2rem 1rem;
}
.ordershop{
    width: 100%;
    min-height: 45rem;
    background: rgb(99, 170, 231);
    
}
.bask{
    
}
.som{

    position: absolute;
        bottom: 3.8rem;
        right: 1.2rem;
}
@media (max-width: 450px){
    .card{
        font-size: 1rem;
        width: 80%;
        height: 20rem;
        justify-content: space-around;
    }
    .lnk{
        font-size: 1.5rem;
    }
    .divimg{
        margin-top: 2vh;
    }
    .card_img{
    
        width: 80%;
        height: 10rem;
    }
