.name{
    margin: 0;
    padding: 0;
    width: 50%;
    height: 30rem;
    color: #000;
    
    position: relative;
}  
.divim{
    width: 100%;
    height: 100%;
    overflow: hidden;
} 
.name_img{
    object-fit: cover;
    transform-origin: left;
    transition: all 1s ease-in;
    width: 100%;
    height: 100%;
    opacity: .5;
    background: #000;
}
.h1{
    font-size: 4rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-style: italic;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%,-50%);
    opacity: 1;
    color: rgb(193, 64, 64);
    z-index: 10000;
}
.name_img:hover{
    transform: scale(1.12)
}
@media(max-width:999px){
    .name{
        width: 100%;height: 15rem;
    }
}