.section{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    width: 90%;
    margin:  auto;
    position: relative;
    color: black;
}
.card{
    width:30em;
    height: 34rem;
    display: flex;
    
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    border: 1px solid rgb(37, 118, 189);
    box-shadow: 0 1rem 2rem rgb(37, 118, 189);
    border-radius: 4px;
    margin: 2rem 0;
    position: relative;
}

.link{
    text-decoration: none;
    margin: 0 auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.divimg{
    margin: 0 auto;
    width: 75%;
    height: 15rem;
    margin: 1rem;
    margin-top: -1rem;
    overflow: hidden;
    
    
}
.card_img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: all 1s ease;
}

.card_img:hover{
    transform: scale(1.085);
    overflow: hidden;
}

.card_name{
    font-family: sans-serif;
    margin-bottom: 1.3rem;
    color: #111;
}
.card_text{
     margin:0 1rem 1rem 1rem;
     color: #111;
}
.ordershop{
    width: 100%;
    min-height: 45rem;
    background: rgb(99, 170, 231);
    
}
.Button{
    width: 100%;
    padding: 2rem 0;
    display: flex;
    justify-content: center
}
.delete{
    color: #111 white;
    font-weight:400;
    font-size: 4vh;
    position: absolute;
    top: 1vh;
    right: 2vh;
    cursor: pointer;
    transition: all 1s ease;
}
.delete:hover{
    color: rgb(92, 90, 88);
    transform: translateY(1px);
}
.delete:active{
    color: rgb(53, 51, 51);
    transform: translateY(0);
}
.lnk{
    color: rgb(11, 50, 113);
    width: 100%;
    display: block;
    font-size: 2rem;
    text-align: center;
}
@media (max-width: 450px){
    .card{
        font-size: 1rem;
        width: 90%;
        height: 25rem;
    }
    .lnk{
        font-size: .9rem;
    }
    .divimg{
        margin-top: 1rem;
        height: 12rem;
    }
    
}