.post_section{
    width: 100%;
    
}
.post_target{
    width: 70%;
    min-height: 100vh;
    margin: 0 auto;
    padding: 3vh;
    box-shadow: 0 2vh 1vh #111;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}
.post_img_box{
    width: 50%;
    height: 45vh;
   
    padding: 3vh;
}
.post_img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}

.post_h3_box{
    width: 90%;
   
    min-height: 8vh;
    padding-top: 3vh;
    
}
.post_h3{
    direction: rtl;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-style: italic;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 1px;
    word-break: keep-all;
    max-width: 80%;
    margin: 0 auto;
}
.post_text_box{
    width: 90%;
    
    min-height: 20vh;
    padding-top: 3vh;
}
.post_text{
    direction: rtl;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-style: italic;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 1px;
    word-break: keep-all;
    text-indent: 2vh;
    max-width: 80%;
    margin: 0 auto;
    word-spacing: 5px;
   padding: 3vh;

}

@media(max-width:1200px){
 .post_img_box{
     width: 75%;
    height: 45vh;
 }   
}
@media(max-width:1200px){
    .post_img_box{
        width: 75%;
       height: 45vh;
       padding-top: 1.5vh;
    }
    .post_h3_box{
        padding-top: 1.5vh;
    }
    .post_h3{
        font-size: 2.5vh;
    }   
    .post_text_box{
        padding-top: 1.5vh;
        width: 98%;
    }
    .post_text{
        font-size: 1.8vh;
       width: 100%;
    }
   }

   @media(max-width:500px){
    .post_img_box{
        width: 100%;
       height: 45vh;
    }   
   }