.footer{
    width: 100%;
    height: 13rem;
    background: #111;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-flow: row wrap;
}
.foot_text{
    font-size: 2.3rem;
    color: aliceblue;
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif;
    transition: all .3s ease-in;
}
.link{
    margin-left: 3rem;
    width: 3rem;
    height: 3rem;

}
.foot_img:hover{
    transform: scale(1.2)
}
.foot_img{
    width: 3rem;
    height: 3rem;
   object-fit: cover
}
.foot_text:hover{
    
    
    animation: cooler 1s forwards;
}

@keyframes cooler{
    0%{
        color: aliceblue;
        transform: rotate(0deg);
     }
    70%{
       color: rgb(177, 248, 225);
       transform: rotate(3deg);
    }
    100%{
        color: rgb(127, 255, 212);
        transform: rotate(0deg);
    }
    
}
@media (max-width:550px){
.foot_text{
    font-size: 1.5rem;
}
.link{
    margin:0 1rem;
}
}