.store{
    height: 3rem;
    width: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    
    border-radius: 50%;
    background: rgb(8, 109, 132);
    position: fixed;
    right: 4rem;
    top: 4rem;
    z-index: 10;
    border: 1px solid rgb(14, 40, 171);
}
.store:hover{
    background: rgb(35, 166, 186);
}
.sometotal{
    border-radius: 50%;
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(231, 85, 48);
    position: fixed;
    right: 3.7rem;
    top: 3.7rem;
    z-index: 100;
}