.ui{
    width: 100%;
    height: 100vh;
    background: rgb(48, 45, 45, .3);
    position: fixed;
    top: 0;
    left: 0%;
   
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 129000;
}
.modal{
    width: 65%;
    height: 30vh;
    background: rgb(189, 181, 181);
    position: relative;
    z-index: 1260000;
}
.pra{
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 3vh;
    font-family: Georgia, 'Times New Roman', Times, serif;
    letter-spacing: 1px;
    padding: 0 3vh;

}
.yes{
    background: none;
    outline: none;
    border: none;
   
    font-size: 4vh;
    font-weight: 500;
    color: rgb(168, 13, 13);
    position: absolute;
    bottom: 0%;
    left: 12%;
    transform: translate(-50%,-50%);
}
.cancel{
    background: none;
    outline: none;
    border: none;
    position: absolute;
    font-size: 4vh;
    font-weight: 500;
    color: rgb(48, 46, 46);
    bottom: 0%;
    left: 5%;
    transform: translate(-50%,-50%);
}
@media (max-width: 1000px){
    .modal{
        width: 80%;
    }
    .pra{
        padding: 1vh;
    }
    .yes{
        left: 30%;
    }
    .cancel{
        left: 15%;
    }
}