.buildpost{
    width: 100%;
    min-height: 100vh;
    background: rgb(233, 174, 182);
}
.box_choice{
    position: fixed;
    z-index: 40000;
    width: 100%;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}
.choice{
    padding: .5rem 1rem;
    font-size: 3.5vh;
    color: rgb(27, 27, 27);
    outline: none;
    border: none;
    background: rgb(40, 113, 223);
    transition: all 1s ease
    ;
    border-radius: 5px;
}
.choice:hover{
    background:rgb(87, 130, 194) ;
}
.choice:disabled{
    cursor: not-allowed;
    background: rgb(100, 97, 97);
}
.choice:disabled:no-button{

}
.builder{
    padding-top: 8vh;
}
.text{
    
    width: 90%;
    min-height: 20vh;
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    margin: 0 auto;
    padding-bottom: 4vh;
}

.text_input{
    max-width: 90%;
    min-width: 90%;
    padding: 2vh;
    min-height: 15vh;
    max-height: 55vh;
    border: none;
    outline: none;
    overflow-x: hidden;
    font-size: 3vh;
    z-index: 80;
    overflow: hidden;
    direction: rtl;
}
.builder{
    width: 100%;
    min-height: 100vh;
    
    
}

.remowe{
    position: absolute;
    top: 8%;
    left:4%;
    cursor: pointer;
    z-index: 200;
    font-size: 2rem;
}
.name{
    
    height: 2rem;
    border-radius: 5px;
    outline: none;
    border: none;
    padding: .4rem .5rem;
}
.newitem{
    margin-top: 1rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3rem 0 5rem 0;
}
.img_input{
    margin-top: 2rem;
    padding: .7rem 1rem;
    border-radius: 5px;
    background: rgb(66, 104, 185);
}

.h1_input{
    margin-top: 1rem;
    max-width: 90%;
    min-width: 90%;
    min-height: 6rem;
    max-width: 10rem;
    resize: none;
    outline: none;
    border: none;
    padding: 2vh;
    direction: rtl;
}


@media(max-width:630px){

    .remowe{
        font-size: 1.5rem;
    }
    
    .choice{
        font-size: 1rem;
        padding: .3rem .6rem
    }
    .name{
       width: 7rem;
       font-size: 1rem;
    }
    .name::placeholder{
        font-size: .5rem;
    }
}


@media(max-width:500px){

    .remowe{
        font-size: 1.5rem;
    }
    
    .choice{
        font-size: 1rem;
    }
    .name{
       width: 7rem;
       font-size: 1rem;
    }
    .name::placeholder{
        font-size: .5rem;
    }
}