.button{
    padding: 1rem 2rem;
    background-color: rgb(29, 28, 28);
    color: rgb(184, 163, 163);
    border-radius: 4px;
    box-shadow: 0 1rem 1.2rem rgb(29, 28, 28);
    border: 1px solid #111;
    font-size: 2rem;

}
.button:disabled{
    cursor: not-allowed;
    background: rgb(109, 107, 107);
}
.button:disabled:hover{
    box-shadow: 0 1rem 1.2rem rgb(29, 28, 28);
    border: 1px solid #111;
    background: rgb(109, 107, 107);
}
.button:disabled:no-button{
    border: none;
}
.button:hover{

    box-shadow: 0 .9rem 1.2rem rgb(29, 28, 28);
    border: 1px solid #111;
    background-color: rgb(46, 43, 43);
}