
.wth{
    margin-top: 2rem;
    width: 30%;
    height: 45%;
    margin-left: 1rem;
}
.load{

    width: 100%;
    min-height: 100%;
    margin-top: 4rem;
    display: flex;
    align-items: center;
    
    justify-content: space-around;
    
}
.dive{
    margin-top: 2rem;
    display: flex;
    text-align: center;
    
    flex-direction: column;
}
.h4{
    font-size: 3rem;
    color: #111;
    font-weight: 400;
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif; 
}
.txt{
    font-size: 1.5rem;
    color: #111;
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif; 
    word-break: keep-all;
    margin: 0 auto;
    max-width: 80%;
    padding-bottom: 4vh;
}
@media (max-width: 900px){
    .mahsol{
        flex-direction: column;
    }
    .h4{
        font-size: 2.3rem;
    }
    .txt{
        font-size: 1rem;
        
    }
    .dive{
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
    }
    .load{
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
    }
    .wth{
        width: 70%;
        height: 45vh;
    }
}

@media (max-width: 1200px){
    .mahsol{
        flex-direction: column;
    }
    .h4{
        font-size: 2.3rem;
    }
    .txt{
        font-size: 1rem;
        margin: 0 1rem;
    }
    .load{
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
    }
    
}