.name{
    width: 50%;
    height: 30rem;
    background:rgba(230, 238, 234, 0.735);
    z-index: 1000;
  position: relative;
    
    
}

.h1{
    font-size: 3rem;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-style: italic;
    text-transform: uppercase;
    font-weight: 300;
    word-break: keep-all;
    z-index: 2000;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.name::before{
    width: 20rem;
    height: 100%;
    background: red;
}
.divimg{
    width: 100%;
    height: 100%;
    overflow: hidden;
    
}
.img_psg{
    object-fit: cover;
    opacity: .3;
    transition: all 1.4s ease;
}
.img_psg:hover{
    transform: scale(1.2);
}
@media(max-width:999px){
    .name{
        width: 100%;
        height: 15rem;
    }
    .h1{
        word-break: keep-all;
    }
    .img_psg{
        width: 100%;
    }
}
@media(max-width:500px){
    .name{
        width: 100%;
        height: 15rem;
       
    }
    .h1{
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        height: 15rem;
        font-size: 2rem;
       
    }
}