.search{
    margin-left: 4vh;
    width: 20%;
    height:100% ;
    display: flex;
   
    
    align-items: center;
    float: right;
    margin-right: 1rem;
    position: relative;
}
.input_search{
    width: 100%;
    height: 2rem;
    background: rgb(108, 106, 106);
    color: beige;
    border-radius: 6px;
}
.input_search::placeholder{
    color: aliceblue;
    text-align: center;
}
.img_search{
 margin-left:-2rem; 
 border-radius: 5px;
 width: 1.5rem;
 height: 1.5rem;
}
@media(max-width:999px){
    .search{
        width: 13rem;
    }

}
.part{
    color: rgb(23, 23, 24);
    width: 5rem;
    min-height: 0;
    font-size: 1rem;
    border: 1px solid #fff;
    display: flex;
    flex-direction: column;
    position: absolute;
    top:4.2rem;
    z-index: 2000;

}
@media(height:0){
    .part{
        display: none;
    }
}
.minprt{
    width: 5rem;
    height: 1.3rem;
    background: rgb(172, 233, 229);
    
    
}