
.flowers{
   width: 100%;
    min-height: 100vh;
    margin: auto;
   position: relative;
    background: rgb(14, 83, 144);
}
.head{
    width: 100%;
    height: 6rem;
    background:linear-gradient(to left top,rgb(11, 11, 12),#111, rgb(32, 26, 26));
    display: flex;
    align-items: center;
}

.section{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    width: 95%;
    margin:  auto;
    
    color: black;
}
.card{
    width:30em;
    height: 34rem;
    display: flex;
    
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    border: 1px solid rgb(37, 118, 189);
    box-shadow: 0 1rem 2rem rgb(37, 118, 189);
    border-radius: 4px;
    margin: 2rem 0;
    position: relative;
}

.link{
    text-decoration: none;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.card:hover{

    transform: scale(1.0015);
   

}
.divimg{
    width: 75%;
    height: 15rem;
    margin: 1rem;
    margin-top: -1rem;
    overflow: hidden;
}
.card_img{
    width: 100%;
    height: 100%;
    
    object-fit:contain;
    transition: all 1s ease;
}

.card_img:hover{
    transform: scale(1.065);
    overflow: hidden;
}

.card_name{
    font-family: sans-serif;
    margin-bottom: 1.3rem;
    color: #111;
}
.paginate{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4vh;
}
.card_text{
     margin:0 1rem 1rem 1rem;
     color: #111;
}
.ordershop{
    width: 100%;
    min-height: 45rem;
    background: rgb(99, 170, 231);
    
}
.Button{
    width: 100%;
    padding: 2rem 0;
    display: flex;
    justify-content: center
}
.lnk{
    color: rgb(11, 50, 113);
    width: 100%;
    display: block;
    font-size: 2rem;
    text-align: center;
}
.ulnumber{
    display: flex;
    padding: 2rem;
    width: 100%;
    justify-content: center;
    
    
}
.number{
    list-style: none;
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
    background: #fff;
    margin-left: .4rem;
    display: flex;
    justify-content: center;
    cursor: pointer;
}
.active{
    list-style: none;
    width: 2.2rem;
    height: 2.2rem;
    font-size: 1.4rem;
    background: #fff;
    margin-left: .4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: rgb(194, 22, 65);
    margin-top: -.1rem;
}
.Button{
    width: 100%;
    margin-top: 3rem ;
    display: flex;
   justify-content:center 
}
@media (max-width: 450px){
    .card{
        font-size: 1rem;
        width: 90%;
        height: 30rem;
    }
    .lnk{
        font-size: .9rem;
    }
    .card_img{
        width: 100%;
        height: 15rem;
    }
}