.vali{
    position: fixed;
   
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 120;
    background-color: rgba(225, 212, 212, 0.463);
    display: flex;
    align-items: center;
    justify-content: center;
}
.mode{
    width: 40%;
    min-height: 25rem;
    background: rgb(9, 70, 101);
    z-index: 1000;
    position: fixed;
    opacity: 1;
    
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    transition: all 3s ease-out;

}
.Modal{
    margin: 1rem;
}
.someorder{
    font-size: 1.4rem;
    font-weight: 300;
    color: antiquewhite;
    letter-spacing: 1px;
    margin: .6rem;
}
.ok{
    color: rgb(29, 184, 27);
    background: none;
    outline: none;
    border: none;
    font-size: 1.7rem;
    margin: 1rem;
    font-weight: 300;
}
.ok:hover{
    color: rgb(9, 106, 20);
}
.cancel{
    color: rgb(150, 51, 20);
    background: none;
    outline: none;
    border: none;
    font-size: 1.7rem;
    font-weight: 300;
    margin: 1rem;
}
.cancel:hover{
    color: rgb(93, 26, 6);
}
.ModalOpen {
    animation: openModal 0.4s ease-out forwards;
}

.ModalClosed {
    animation: closeModal 1s ease-out forwards;
}

.fade-slide-enter {

}

.fade-slide-enter-active {
    animation: openModal 0.4s ease-out forwards;
}

.fade-slide-exit {

}

.fade-slide-exit-active {
    animation: closeModal 1s ease-out forwards;
}

@keyframes openModal {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    50% {
        opacity: 1;
        transform: translateY(90%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes closeModal {
    0% {
        opacity: 1;
        transform: translateY(0);
    }
    50% {
        opacity: 0.8;
        transform: translateY(60%);
    }
    100% {
        opacity: 0;
        transform: translateY(-100%);
    }
}
@media(max-width:1300px){
    .mode{
        width: 60%;
    }
}
@media(max-width:900px){
    .mode{
        width: 80%;
    }
}