.header{
    width: 15rem;
    height: 100vh;
    background: rgb(17, 87, 122);
    position: fixed;
    top: 0;
    right: 0;
    z-index: 40000;
    animation: anim 1.3s forwards;
    

}
.animated{
    animation: anim2 1.3s forwards;
}
.draw{
    width: 100%;
    display: flex;
    align-items: center;
    min-height: 33rem;
    margin-top: 2rem;
    justify-content: space-around;
    flex-direction: column;
}
.zab{
    position: absolute;
    top: 1rem;
    right: 10rem;
}
.zab:hover{
    transform: scale(1.1)
}
.option{
    padding: 4vh;
    width: 4rem;
    height: 3rem;
    background: none;
    border-radius: 50%;
    outline: none;
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color:#fff;
    position: fixed;
    top:6%;
    right: 3%;
    transform: translate(-50%,-50%);
    z-index: 10000;
}
.span{
    width: 100%;
    font-size: 1rem;
    font-weight: 300;
    margin: .1rem;
   padding: 0;
   transition: all 1s ease;
}
.span:hover{
    color: rgb(17, 87, 122)
}
@media (min-width:1229px){

    .option{
        display: none;
    }
}
@media (max-width:500px){

    .option{
        position: fixed;
        top:6%;
        right: 0%;
        transform: translate(-50%,-50%);
        padding: 4vh;

    }
}

@keyframes anim{
    from {
        position: fixed;
    top: 0;
    right:-20rem ;
    }
    to{
        position: fixed;
    top: 0;
    right: 0;
    }
}

@keyframes anim2{
    from {
        position: fixed;
    top: 0;
    right:0 ;
    }
    to{
        position: fixed;
    top: 0;
    right: -20rem;
    }
}